.ui-dialog {
    display: flex;
    flex-direction: column;
}

.ui-dialog-paper {
    margin: 0px;
    width: 100%;
    padding: 1rem;
    --tw-text-opacity: 1;
    color: rgba(41, 37, 36, var(--tw-text-opacity));
}

.dark .ui-dialog-paper {
    --tw-bg-opacity: 1;
    background-color: rgba(41, 37, 36, var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgba(231, 229, 228, var(--tw-text-opacity));
}

.ui-dialog-paper {
    background-color: #1b97f4;
}

.ui-dialog-paper:not(.fullScreen) {
    border-radius: 0.25rem;
    border-radius: 1rem;
    border-width: 1px;
    background-color: #1b97f4;
}

.ui-dialog-title {
    position: relative;
    margin-bottom: 1.25rem;
    display: flex;
    justify-content: center;
    text-align: center;
    font-family: "VT323", ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    font-size: 1.5rem;
    line-height: 2rem;
}

.dark .ui-dialog-paper{
    background-color: #1b97f4;
}
.ui-button {
    border-width: 1px;
    border-color: transparent;
    font-family: "VT323", ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}.ui-button:focus {
    outline: 2px solid transparent;
    outline-offset: 2px;
}.ui-button {
    background-color: orangered;
    border: 2px solid orange;
    /* box-shadow: 2px 2px 8px orange; */
}

.dark{
    background-color: orangered;
    box-shadow: 2px 2px 8px orange;
}

.ui-button-standard:not(:disabled) {
    background: #FFFFFF;
    box-shadow: 0.2em 0.2em calc(0.2em * 2) #BFBFBF, calc(0.2em * -1) calc(0.2em * -1) calc(0.2em * 2) #FFFFFF;
}

.ui-button-standard:not(:disabled):active {
    background: #F5F5F4;
    box-shadow: inset 0.2em 0.2em calc(0.2em * 2) #BBBBB4, inset calc(0.2em * -1) calc(0.2em * -1) calc(0.2em * 2) #FFFFFF;
}

.dark .ui-button-standard:not(:disabled) {
    background: #292524;
    box-shadow: 0.2em 0.2em calc(0.2em * 2) #1D1A19, calc(0.2em * -1) calc(0.2em * -1) calc(0.2em * 2) #332E2D;
}

.dark .ui-button-standard:not(:disabled):active {
    background: #292524;
    box-shadow: inset 0.2em 0.2em calc(0.2em * 2) #1D1A19, inset calc(0.2em * -1) calc(0.2em * -1) calc(0.2em * 2) #332E2D;
