.game-block {
    height: 100%;
    width: 100%;
    aspect-ratio: 1 / 1;
}

@supports not (aspect-ratio: 1 / 1) {
    .game-block::before {
        float: left;
        padding-top: 100%;
        content: "";
    }

    .game-block::after {
        display: block;
        content: "";
        clear: both;
    }
}

.game-block.game-block-border {
    border-width: 1px;
    border-bottom-width: 0px;
    border-right-width: 0px;
    border: blue;
    opacity: 0.8;
    -webkit-filter: brightness(117.5%);
            filter: brightness(117.5%);
}

.block-color::after {
    border-radius: 0;
    display: block;
    position: absolute;
    content: ' ';
    top: 2px;
    bottom: 2px;
    left: 2px;
    right: 2px;
    opacity: 0.8;
    -webkit-filter: brightness(117.5%);
            filter: brightness(117.5%);
}

.block-color::before {
    border-radius: 0;
    display: block;
    position: absolute;
    content: ' ';
    top: 1px;
    bottom: 1px;
    left: 1px;
    right: 1px;
    /* @apply rounded-sm; */
    opacity: 0.8;
    -webkit-filter: brightness(117.5%);
            filter: brightness(117.5%);
    margin: -1px;
}

